@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@400;700&display=swap);
* {
  box-sizing: border-box;
}

body {
  font-family: 'Roboto', sans-serif;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  height: 100vh;
  overflow: hidden;
  margin: 0;
  background-image: linear-gradient(90deg, #4481eb 10%, #04befe 90%);
  color: #f5f5f5;
  text-align: center;
  /* transition: background-color 0.3s linear; */
}

body.on {
  background: linear-gradient(90deg, #e64980 5%, #ff8787 95%);
}

#wrapper {
  padding-top: 25px;
  width: 350px;
  height: 250px;
  background: rgba(255, 255, 255, 0.1);
  border-radius: 5px;
  margin-left: 10px;
  margin-right: 10px;
  margin-bottom: 5px;
}

#wrapper.start {
  background: rgba(255, 255, 255, 0.3);
}

#timer-label {
  margin-top: 5px;
  margin-bottom: 0;
}

#time-left {
  margin-top: 0px;
  margin-bottom: 0px;
  font-size: 70px;
}

.settings {
  display: flex;
  justify-content: center;
}

.option {
  background: rgba(255, 255, 255, 0.1);
  border-radius:5px;
  width: 170px;
  margin: 5px;
  display:flex;
  flex-direction: column;
  align-items: center;
}

.option.start {
  background: rgba(255, 255, 255, 0.3);
}

.wrap-settings {
  display:flex;
  align-items: center;
}

p {
  width: 40px;
}

button.simple {
  background-color: rgba(0, 0, 0, 0.4);
  transition: background-color 0.3s;
  color: white;
  height: 2em;
  width: 90px;
  border: none;
  border-radius: 3px;
  margin: 5px;
  cursor: pointer;
}

button.simple:hover {
  background-color: rgba(0, 0, 0, 0.2);
}

button.simple:focus {
  outline: 0;
  background-color: rgba(255, 255, 255, 0.2);
}

button.specific {
  background-color: white;
  color: #40514e;
  height: 2em;
  width: 90px;
  border: none;
  border-radius: 3px;
  margin: 5px;
  cursor: pointer;
  font-size: 20px;
  font-weight: bold;
  box-shadow: 0 3px 0px 0px rgba(255, 255, 255, 0.8);
}

button.specific:focus {
  outline: 0;
}

button.btn-settings {
  position: relative;
  width: 30px;
  height: 25px;
  margin: 10px;
  font-size: 20px;
}

